import React from 'react';
import Link from '@material-ui/core/Link';
import MyAppBar from './components/MyAppBar';
import * as data from './data.json';
import Areas from './pages/Areas';
import Timelines from './pages/Timelines';
import { BrowserRouter as Router, Route } from "react-router-dom";

import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>      
      <Route exact path="/" component={Areas} />
      <Route path="/about" component={About} />
      <Route path="/areas" component={Areas} />
      <Route path="/timelines/:area" component={Timelines} />        
    </Router>
  );
}

function About() {
  return (
    <React.Fragment>
      <MyAppBar></MyAppBar>
      <Box mt={10} mx={2}> 
        <Typography variant="h5" gutterBottom>このサイトについて</Typography>
        <p>
          選挙のとき、誰に投票していいのか分からなくなりませんか？<br/>
          ツイセンは、候補者のTwitterタイムラインをチェックできるウェブ・サイトです。<br/>
          総務省が公開している<a href="http://www.soumu.go.jp/main_content/000632561.pdf">選挙区候補者データ</a>のウェブサイト情報から、記載されたTwitterアカウントを抽出しています。
        </p>
        <p>
          作成者: <a href="https://twitter.com/mshk">@mshk</a>
        </p>
      </Box>
    </React.Fragment>
  )
}

export default App;